<script setup>
import { onMounted, ref } from "vue";
import { useRoute } from "vue-router";
const route = useRoute();

onMounted(() => {});

const content = ref(null);

const http = async () => {
  try {
    let response = await fetch(
      `https://newsapi.org/v2/everything?qInTitle=${route.query.title}&author=${route.query.author}&from=${route.query.publishedAt}&to=${route.query.publishedAt}&apiKey=1e4f3628160f4bfb8d6e380d56aa703c`
    );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    content.value = await response.json();
    console.log(content.value);
  } catch (error) {
    console.error(`Error fetching random recipes: ${error.message}`);
  }
};
http();
